v(_vm._s(_vm.$t('Sponsor Track 1')))]),_c('img',{staticClass:"arrow",attrs:{"src":"/assets/img/arrow.svg","alt":"arrow"}})]),_c('div',{staticClass:"flex1 flex-center"},[_c('div',{staticClass:"timer"},[_vm._v("11:00 - "+_vm._s(_vm.$t('Start')))])]),_c('div',{staticClass:"track-title flex1 hide-mobile"},[_c('p',[_vm._v(_vm._s(_vm.$t('Sponsor Track 2')))]),_c('img',{staticClass:"arrow",attrs:{"src":"/assets/img/arrow.svg","alt":"arrow"}})])]),_c('div',{staticClass:"line-center"},[_c('Card',{attrs:{"position":"left","track":"Sponsor Track 1","time":"11:00 - 12:00","avatar":"talia-nassi.png","title":"Testing in Production","author":"Talia Nassi","desc":"How do you know your feature is working perfectly in production? If something breaks in production, how will you know? Will you wait for a user to report it to you? What do you do when your staging test results do not reflect current production behavior? In order to test proactively as opposed to reactively, try testing in production! You will have an increased accuracy of test results, your tests will run faster due to the elimination of bad data, and you will have higher confidence before releases. This can be accomplished through feature flagging, canary releases, setting up a proper CI/CD pipeline, and data cleanup. You will leave this talk with strategies to mitigate risk, to better your understanding of the steps to get there, and to shift your company’s testing culture, so you can provide the best possible experience to your users. At the end of the day, we don't care if your features work in staging, we care if they work in production."}}),_c('Card',{attrs:{"position":"right","track":"Sponsor Track 2","time":"11:00 - 12:00","avatar":"cem-sancak.jpg","title":"Canlıdan Go Kesitleri","author":"Cem Sancak","desc":"Go ile geliştirmeyi keyifli kılan ana mekaniklerden ikisi, eş zamanlı işleme (concurrency) becerileri ve kanallar (channels). Bu konuşmamda üstünde çalıştığım canlı sistemlerde sık sık karşıma çıkan kullanım senaryolarını ve örnek kodları paylaşıyor olacağım. Bana ayrılan zamanın sonunda, dinleyenlere eş zamanlı programlama ile düşünebilmenin servis tasarımlarını nasıl değiştirebileceğini aktarabilmeyi ve Go'yu başarılı kılan parçalar hakkında biraz daha derin bilgi sunmayı hedefliyorum."}})],1),_c('div',{staticClass:"line-center"},[_c('Card',{attrs:{"position":"left","track":"Sponsor Track 1","time":"12:00 - 13:00","avatar":"kemal-akkoyun.jpg","title":"Building Observable Go Services","author":"Kemal Akkoyun","desc":"In modern days, we run our applications as loosely coupled micro-services on distributed, elastic infrastructure as (mostly) stateless workloads. Under these circumstances, observability has become a key attribute to understand how our applications run and behave in action, in order to provide highly available and resilient service. There exist several observability signals, such as “log”, “metric”, “tracing” and “profiling” that can be collected from a running service, which we can also call pillars of observability. Using these signals, we can create real-time, actionable alerts, create panels where we can monitor applications closely, and perform in-depth analysis to find the root of the systems’ failures. Within the Go and CNCF ecosystem, there are a variety of tools that can collect and make these observable signals useful. During this talk, Kemal will first introduce the tools that can be embedded in the services to make critical services observable, and share the patterns that will enable them to be used efficiently in the applications and services. Moreover,  he will demonstrate how to use these collected signals in real-life scenarios, using tools within the CNCF ecosystem (Loki, Prometheus, OpenTelemetry, Jaeger, Conprof). He also aims to share the methods that are used to build and run applications running under heavy-traffic, and to understand the origin of the problems encountered in running systems."}}),_c('Card',{attrs:{"position":"right","track":"Sponsor Track 2","time":"12:00 - 13:00","avatar":"ibrahim-serdar-acikgoz.jpg","title":"Go Struct Tag'lerini kullanmak","author":"Ibrahim Serdar Açıkgöz","desc":"Go'da çogu zaman verilerin JSON, YAML biçimlerine serileştirilmede bize yardımcı olmasi için veri tiplerine etiketler ekliyoruz. Peki bunun nasıl çalıştıgını hiç düşündünüz mü? Ya da farklı amaçlarda kullanma fırsatınız oldu mu? Gelin hep beraber bu özelligin nasıl çalıştıgına bir bakalım ve kendi amaçlarımız için neler yapılabilecegini tartışalım."}})],1),_c('div',{staticClass:"line-center"},[_c('Card',{attrs:{"position":"left","track":"Sponsor Track 1","time":"13:00 - 14:00","avatar":"teiva-harsanyi.jpg","title":"Mechanical Sympathy in Go","author":"Teiva Harsanyi","desc":"“You don’t have to be an engineer to be a racing driver, but you do have to have Mechanical Sympathy” - Jackie Steward, 3-time F1 world champion. Applying mechanical sympathy to software means that understanding how the hardware is working can help us in designing more efficient algorithms, data structures, etc. In this talk, we will see concrete examples applied to Go where having a mechanical sympathy can lead in application performance optimization."}}),_c('Card',{attrs:{"position":"right","track":"Sponsor Track 2","time":"13:00 - 14:00","avatar":"oguzhan-yilmaz.jpeg","title":"Go Performans Rehberi","author":"Oğuzhan Yılmaz","desc":"Go programlama dili her ne kadar performans odaklı olsa da yazılım geliştirirken vereceğimiz kararlar bunu çok etkiliyor. Bazen küçük bir string birleştirmesi veya standard bir for loop'u cloud ortamlarında bile performans problemlerine yol açabiliyor. Bu konuşmada Go ile yazılım geliştirirken günlük hayatta kullandığımız tip, fonksiyon ve tasarımları uygularken nasıl daha performanslı hale getirebileceğimizi ve performans ile alakalı Go'nun sunduğu native fonksiyonları ve hileleri keşfedeceğiz. Bu rehber sayesinde küçük ip uçları ile nasıl büyük farklar yaratılabilir hep beraber göreceğiz."}})],1),_c('div',{staticClass:"line-center"},[_c('Card',{attrs:{"position":"left","track":"Sponsor Track 1","time":"14:00 - 15:00","avatar":"fahri-yardimci.jpg","title":"Distributed Tracing on Go using OpenTelemetry","author":"Fahri Yardımcı","desc":"In the microservices era, monitoring applications become harder due to distributed and asynchronous data flow, therefore quickly identifying errors and performance problems is crucial. Tracing will help identify which part of the code is problematic, or causing latency. With the multiple microservices, Distributed tracing is used for identifying the applications and functions which cause errors. OpenTelemetry is a CNCF (Cloud Native Computing Foundation) sandbox project and aims to production-ready in the second half of 2020. In this talk, I'll talk about what is Tracing, Spans, Injecting/Extracting traces via HTTP, and Tracing models with the OpenTelemetry implementations."}}),_c('Card',{attrs:{"position":"right","track":"Sponsor Track 2","time":"14:00 - 15:00","avatar":"erhan-yakut.jpeg","title":"Go Projelerinde Clean Code Mimarisinin Uygulanması","author":"Erhan Yakut","desc":"Geçmişten günümüze yazılım alanında birçok şey değişti; programlama dilleri, frameworkler, sistemler... Bunların esasında birer araç olduğunu düşünürsek aslında değişmeleri gayet normal. Buna karşılık Clean Code prensipleri gibi tam anlamıyla değişmeyen şeyler de var. Sunumumda Clean Code prensiplerinin Go projelerinde uygulanmasını anlatacağım."}})],1),_c('div',{staticClass:"line-center"},[_c('Card',{attrs:{"position":"left","track":"Sponsor Track 1","time":"15:00 - 16:00","avatar":"amit-mishra.jpg","title":"Scaling a playback platform: Infinity and beyond","author":"Amit Mishra","desc":"I am part of Architect team which is responsible for building platform to live stream the contents at Fox. As I first time speaker , I would like to share our story and challenges we face everyday while streaming contents. I would like to share how Go helped us building a playback platform which can handle these challenges and serve magical moment to millions of concurrent users without any issue."}}),_c('Card',{attrs:{"position":"right","track":"Sponsor Track 2","time":"15:00 - 16:00","avatar":"pamir-erdem.jpg","avatarSecond":"oguz-yarimtepe.jpg","title":"Developing Custom Kubernetes Schedulers","author":"Pamir Erdem, Oğuz Yarımtepe","desc":"Without best practices; deploying an application on kubernetes , by no means make it production-grade. Sometime we need to scale our application depending on customers needs. In this talk we are going to develop a custom kubernetes scheduler for high available multi zonal scheduler."}})],1),_c('div',{staticClass:"line-center"},[_c('Card',{attrs:{"position":"left","track":"Sponsor Track 1","time":"16:00 - 17:00","avatar":"ekta-garg.jpg","title":"Running Go in browsers using WASM","author":"Ekta Garg","desc":"Web Assembly AKA WASM is a new way of running high-level languages into the browsers. Cases where you need to do complex computations like gaming, 3d rendering, AI stuff, etc in browser using any language of your choice (which has support for WASM). If there are programs, algorithms written in GO, you can simply port them to the browser using WASM. It's a concept, not a language which you would require to code in, it's a binary format that gets ported into the browser directly. It gives a great performance, portability, no more deployment dependency, offers a variety of languages to code in best suitable to the nature of your problem."}}),_c('Card',{attrs:{"position":"right","track":"Sponsor Track 2","time":"16:00 - 17:00","avatar":"jaana-dogan.jpg","title":"Go Derleyecisini Kendisinden Dinlemek","author":"Jaana Dogan","desc":"Çoğunluğun sandığının aksine, Go derleyicisini anlamak için derleyiciler konusunda uzman olmaya gerek yoktur. Go araçları kendilerini anlatabilmek ve debug edilebilmelerini sağlamak için birden fazla seçenek sunmaktadır. Go araçlarını ve derleyicisini daha yakından tanıyarak derleyici optimizasyonlarını görebilir, kendi programlarınızı makine kodundan denetleyebilir, modern derleyicilerin nasıl çalıştığını öğrenebilir ve Go diline destek verebilirsiniz. Bu konuşmada hem Go kullanıcılarının hem de geliştiricilerinin günlük hayatlarına katkıda bulunacak birçok araçtan ve yöntemden bahsedeceğiz."}})],1),_c('div',{staticClass:"line-center"},[_c('Card',{attrs:{"position":"left","track":"Sponsor Track 1","time":"16:00 - 17:00","avatar":"endre-simo.jpg","title":"Face detection in Go and Webassembly","author":"Endre Simo","desc":"Year-by-year the Go language is evolving as it increases the projects and libraries built around it, even though the Go ecosystem is still well behind its competitors in terms of computer vision, machine learning, data analysis etc."}}),_c('div',{staticClass:"empty-card custom-card-right flex1 mt-5 hide-mobile"})],1),_c('div',{staticClass:"line-center"},[_c('Card',{attrs:{"position":"left","track":"Sponsor Track 1","time":"17:00 - 18:00","avatar":"dave-cheney.jpg","title":"High performance JSON parsing","author":"Dave Cheney","desc":"This is a talk about the design strategies I employed to build a package that can tokenise JSON 8-10x faster than the standard library.In the talk I'll cover the high level design of the package. I'll show how design a API that avoids allocation and unnecessary copying (this is a major contributor to the performance of the library). Finally I'll touch on some of the optimisation decisions in the package and show how they work in sympathy with inlining and bounds check elimination."}}),_c('Card',{attrs:{"position":"right","track":"Sponsor Track 2","time":"17:00 - 18:00","avatar":"huseyin-guner.jpeg","title":"Go ile 1.5M Throughput","author":"Hüseyin Güner","desc":"Konuşma genel olarak Trendyol'da Java-Go geçişi ile elde ettiğimiz kaynak kazanımı, yüksek scale ölçeği, yaşadığımız zorluklar, karşılaştığımız buglar, çözüm yöntemlerimiz, profiling ve Go ile alakalı deneyimlerin konu olacağı, Go dili ve internalları hakkında olacak."}})],1),_vm._m(4)])])])]),_c('section',{staticClass:"ticket",attrs:{"id":"ticket"}},[_c('img',{staticClass:"gopher hide-mobile",attrs:{"src":"/assets/img/gopher-07.png","alt":"gopher"}}),_c('h2',{staticClass:"title"},[_vm._v(_vm._s(_vm.$t('TICKETS')))]),_c('div',{staticClass:"container"},[_c('div',{staticClass:"tables"},[_c('div',{staticClass:"table mt-4"},[_c('img',{staticClass:"gopher-free",attrs:{"src":"/assets/img/gopher-03.png","alt":"gopher"}}),_c('div',{staticClass:"name"},[_vm._v(_vm._s(_vm.$t('STANDARD')))]),_c('p',{staticClass:"price"},[_vm._v(_vm._s(_vm.$t('Free')))]),_c('ul',[_c('li',{domProps:{"innerHTML":_vm._s(_vm.$t('free_ticket_1'))}})]),_c('a',{staticClass:"buy",attrs:{"href":"https://kommunity.com/goturkiye/events/gophercon-turkey-59b694b8/tickets","target":"_blank"}},[_vm._v(_vm._s(_vm.$t('REGISTER')))])]),_c('div',{staticClass:"table table-red"},[_c('img',{staticClass:"gopher-support",attrs:{"src":"/assets/img/gopher-04.png","alt":"gopher"}}),_c('div',{staticClass:"name"},[_vm._v(_vm._s(_vm.$t('EXCLUSIVE')))]),_vm._m(5),_c('ul',[_c('li',{domProps:{"innerHTML":_vm._s(_vm.$t('exclusive_ticket_1'))}}),_c('li',{domProps:{"innerHTML":_vm._s(_vm.$t('exclusive_ticket_2'))}}),_c('li',{domProps:{"innerHTML":_vm._s(_vm.$t('exclusive_ticket_3'))}}),_c('li',{domProps:{"innerHTML":_vm._s(_vm.$t('exclusive_ticket_4'))}})]),_c('a',{staticClass:"buy buy-red",attrs:{"href":"https://kommunity.com/goturkiye/events/gophercon-turkey-59b694b8/tickets","target":"_blank"}},[_vm._v(_vm._s(_vm.$t('BUY NOW')))])]),_c('div',{staticClass:"table mt-4"},[_c('img',{staticClass:"gopher-community",attrs:{"src":"/assets/img/gopher-05.png","alt":"gopher"}}),_c('div',{staticClass:"name"},[_vm._v(_vm._s(_vm.$t('SPONSORSHIP')))]),_vm._m(6),_c('ul',[_c('li',{domProps:{"innerHTML":_vm._s(_vm.$t('sponsorship_ticket_1'))}}),_c('li',{domProps:{"innerHTML":_vm._s(_vm.$t('sponsorship_ticket_2'))}}),_c('li',{domProps:{"innerHTML":_vm._s(_vm.